:root {
  --blue-accent: #00acdc;
  --white-color: white;
  --light-text-color: #616070;
}

.page {
  color: #060b35;
  width: 200px;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  line-height: 18px;
}

h3, img, a, p {
  margin: 0;
  padding: 0;
}

.product {
  box-sizing: border-box;
  padding: 32px 16px 16px;
}

.product__photo {
  align-items: center;
  width: 160px;
  height: 134px;
  margin: 0 auto 35px;
  padding-left: 3px;
}

.product__title {
  margin: 0;
  font-size: 12px;
  font-weight: 500;
}

.product__code {
  color: var(--light-text-color);
  margin-top: 4px;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.product__review {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  font-size: 10px;
  line-height: 14px;
  display: flex;
}

.product__price {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.product__price-text {
  color: var(--light-text-color);
}

.product__price-value {
  font-size: 16px;
  font-weight: 700;
}

.product__buy {
  background-color: var(--blue-accent);
  color: var(--white-color);
  text-align: center;
  cursor: pointer;
  text-transform: uppercase;
  border: none;
  border-radius: 5px;
  width: 100%;
  padding-block: 11px;
  font-size: 14px;
  font-weight: 700;
  text-decoration: none;
  display: inline-block;
}

.product__buy:hover {
  background-color: var(--white-color);
  color: var(--blue-accent);
  border: 1px solid var(--blue-accent);
}

.stars {
  align-items: center;
  height: 16px;
  display: flex;
}

.stars__star {
  cursor: pointer;
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin: 0;
}

.stars__star:not(:first-child) {
  margin-right: 4px;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

